'use server'
import { addBlogEntry, getAllBlogIds, getBlogEntryWithId } from '../db.js'
export async function addPost(title, id, imgCaption, article) {
  addBlogEntry(id, title, imgCaption, article);
}
export async function getIds() {
  return getAllBlogIds();
}
export async function serverGetBlogEntryWithId(id) {
  return getBlogEntryWithId(id);
}
